import { Button, Modal } from "antd";
import { useCallback } from "react";
import Styles from "./index.module.css";
function macOS() {
  return navigator.userAgent.includes("Mac OS X");
}
function KeyboardDescItem({ kbd, desc }) {
  return (
    <li className={Styles.listItem}>
      <kbd className={Styles.kbd} style={{fontFamily: "Consolas"}}>{kbd}</kbd>
      <span>{desc}</span>
    </li>
  );
}

function HelpModal({ visible, onVisibleChange }) {
  var ctrl = "Ctrl";
  if (macOS()){
    ctrl = "";
  }
  const handleClose = useCallback(() => {
    onVisibleChange?.call(null, false);
  }, [onVisibleChange]);

  return (
    <Modal
      width="100%"
      height="90%"
      style={{ top: "4%" }}
      visible={visible}
      title=""
      onCancel={handleClose}
      footer={
        <Button key="close" type="primary" onClick={handleClose}>
          
        </Button>
      }
    >
      <h3></h3>
      <ul className={Styles.list}>
        <KeyboardDescItem kbd="?" desc="" />
        <KeyboardDescItem kbd={ctrl+"+Z"} desc="" />
        <KeyboardDescItem kbd={ctrl+"+Shift+Z"} desc="" />
        <KeyboardDescItem kbd={ctrl+"+O"} desc="" />
        <KeyboardDescItem kbd="h j k l" desc="" />
      </ul>
      <h3></h3>
      <h4></h4>
      
      <p>
        <kbd className={Styles.kbd}>0-7</kbd>
        <kbd className={Styles.kbd}> ( )</kbd>
      </p>
      <h4></h4>
      <ul className={Styles.list}>
        <KeyboardDescItem kbd="a" desc="" />
        <KeyboardDescItem kbd="A" desc="" />
        <KeyboardDescItem kbd="|" desc="" />
        <KeyboardDescItem kbd="." desc="" />
        <KeyboardDescItem kbd="~" desc="" />
        <KeyboardDescItem kbd="u / U" desc="" />
        <KeyboardDescItem kbd="!" desc="" />
        <KeyboardDescItem kbd="8 / *" desc="" />
        <KeyboardDescItem kbd="# / b" desc="/" />
      </ul>
      <h4></h4>
      <ul className={Styles.list}>
      <KeyboardDescItem kbd="Enter" desc="" />
        <KeyboardDescItem kbd="Shift+Enter" desc="" />
        <KeyboardDescItem kbd={ctrl+"+Enter"} desc="" />
        <KeyboardDescItem kbd="Delete" desc="" />
        <KeyboardDescItem kbd="Shift+Delete" desc="" />
        <KeyboardDescItem kbd="=" desc="" />
        <KeyboardDescItem kbd={ctrl+"+C"} desc="" />
        <KeyboardDescItem kbd={ctrl+"+Shift+C"} desc="" />
        <KeyboardDescItem kbd={ctrl+"+V"} desc="" />
      </ul>
      <h2></h2>
      <ul className={Styles.list}>
        <li></li>
        <li></li>
      </ul>
    </Modal>
  );
}

export default HelpModal;
